<template>
  <div id="ipi-board">
    <div class="left">
      <div class="title" style="text-align:center">
        <h2>订单需求</h2>
      </div>
      <div class="left-content">
        <h3>请选择店铺</h3>
        <!-- 菜单 -->
        <el-menu
          default-active="0-0-0"
          class="el-menu-vertical-demo"
          unique-opened
        >
          <el-submenu
            :index="index + ''"
            v-for="(item, index) in menuPlatFormList"
            :key="index"
          >
            <template slot="title">
              <span
                style="display:inline-block;width:100%"
                @click="onceClick(index)"
                >{{ item }}</span
              >
            </template>
            <el-submenu
              :index="index + '-' + index2"
              v-for="(item2, index2) in showCountryList"
              :key="index2"
            >
              <template slot="title">
                <span
                  style="display:inline-block;width:100%"
                  @click="secondClick(index2)"
                  >{{ item2 }}</span
                >
              </template>
              <el-menu-item
                :index="index + '-' + index2 + '-' + index3"
                v-for="(item3, index3) in lastSiteList"
                :key="index3"
                @click="thirdClick(item, item2, item3)"
              >
                <span>{{ item3 }}</span>
              </el-menu-item>
            </el-submenu>
          </el-submenu>
        </el-menu>
      </div>
    </div>
    <div class="right">
      <div class="search">
        <el-form :inline="true" :model="queryInfo">
          <el-form-item label="日期">
            <el-date-picker
              v-model="queryInfo.date"
              type="date"
              placeholder="选择日期"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
              @change="pickerChange('日期')"
              :disabled="isDateDisabled"
              size="mini"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="或月份">
            <el-date-picker
              v-model="queryInfo.month"
              type="month"
              placeholder="选择日期"
              format="yyyy-MM"
              value-format="yyyy-MM"
              @change="pickerChange('月份')"
              :disabled="isMonthDisabled"
              size="mini"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="时间段">
            <el-select
              v-model="queryInfo.timeSlot"
              placeholder="请选择"
              size="mini"
              :disabled="isMonthDisabled"
            >
              <el-option
                v-for="(item, index) in timeSlot"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="类型">
            <el-select
              v-model="queryInfo.type"
              placeholder="请选择"
              size="mini"
            >
              <el-option
                v-for="(item, index) in typeList"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="产品编码">
            <el-select
              v-model="queryInfo.code"
              placeholder="请选择"
              size="mini"
            >
              <el-option
                v-for="(item, index) in codeList"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="品名">
            <el-select
              v-model="queryInfo.product"
              placeholder="请选择"
              size="mini"
            >
              <el-option
                v-for="(item, index) in productList"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="仓库类型">
            <el-select
              v-model="queryInfo.stockType"
              placeholder="请选择"
              size="mini"
            >
              <el-option
                v-for="(item, index) in stockTypeList"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="仓库名称">
            <el-select
              v-model="queryInfo.stockName"
              placeholder="请选择"
              size="mini"
            >
              <el-option
                v-for="(item, index) in stockNameList"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="目的港">
            <el-select
              v-model="queryInfo.purpose"
              placeholder="请选择"
              size="mini"
            >
              <el-option
                v-for="(item, index) in purposeList"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="mini" @click="search"
              >搜索</el-button
            >
          </el-form-item>
        </el-form>
        <div class="select-base">
          <!-- <div class="select">
            <span>日期：</span>
            <el-date-picker
              v-model="queryInfo.date"
              type="date"
              placeholder="选择日期"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
              @change="pickerChange('日期')"
              :disabled="isDateDisabled"
              size="mini"
            ></el-date-picker>
          </div> -->
          <!-- <div class="select">
            <span>或月份：</span>
            <el-date-picker
              v-model="queryInfo.month"
              type="month"
              placeholder="选择日期"
              format="yyyy-MM"
              value-format="yyyy-MM"
              @change="pickerChange('月份')"
              :disabled="isMonthDisabled"
              size="mini"
            ></el-date-picker>
          </div> -->
          <!-- <div class="select">
            <span>时间段：</span>
            <el-select
              v-model="queryInfo.timeSlot"
              placeholder="请选择"
              size="mini"
              :disabled="isMonthDisabled"
            >
              <el-option
                v-for="(item, index) in timeSlot"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </div> -->
        </div>
        <div class="select-detail">
          <!-- <div class="select">
            <span>类型：</span>
            <el-select
              v-model="queryInfo.type"
              placeholder="请选择"
              size="mini"
            >
              <el-option
                v-for="(item, index) in typeList"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </div> -->
          <!-- <div class="select">
            <span>产品编码：</span>
            <el-select
              v-model="queryInfo.code"
              placeholder="请选择"
              size="mini"
            >
              <el-option
                v-for="(item, index) in codeList"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </div> -->
          <!-- <div class="select">
            <span>品名：</span>
            <el-select
              v-model="queryInfo.product"
              placeholder="请选择"
              size="mini"
            >
              <el-option
                v-for="(item, index) in productList"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </div> -->
          <!-- <div class="select">
            <span>仓库类型：</span>
            <el-select
              v-model="queryInfo.stockType"
              placeholder="请选择"
              size="mini"
            >
              <el-option
                v-for="(item, index) in stockTypeList"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </div> -->
          <!-- <div class="select">
            <span>仓库名称：</span>
            <el-select
              v-model="queryInfo.stockName"
              placeholder="请选择"
              size="mini"
            >
              <el-option
                v-for="(item, index) in stockNameList"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </div> -->
          <!-- <div class="select">
            <span>目的港：</span>
            <el-select
              v-model="queryInfo.purpose"
              placeholder="请选择"
              size="mini"
            >
              <el-option
                v-for="(item, index) in purposeList"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </div> -->
          <!-- <el-button type="primary" size="mini" @click="search">搜索</el-button> -->
        </div>
      </div>
      <div class="table">
        <el-table
          :data="orderDemandList"
          border
          style="width: 100%"
          :max-height="680"
          v-loading="loading"
          element-loading-text="拼命加载中"
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)"
        >
          <el-table-column
            width="60"
            type="index"
            label="序号"
            fixed
          ></el-table-column>
          <el-table-column width="100" prop="1" label="月份"></el-table-column>
          <el-table-column prop="2" label="时段"></el-table-column>
          <el-table-column prop="3" label="渠道"></el-table-column>
          <el-table-column prop="4" label="站点"></el-table-column>
          <el-table-column prop="5" label="国家"></el-table-column>
          <el-table-column prop="6" label="货柜号"></el-table-column>
          <el-table-column prop="7" label="仓库类型"></el-table-column>
          <el-table-column prop="8" label="仓库编码"></el-table-column>
          <el-table-column prop="9" label="仓库名称"></el-table-column>
          <el-table-column prop="10" label="目的港"></el-table-column>
          <el-table-column prop="11" label="类型"></el-table-column>
          <el-table-column
            prop="12"
            width="100"
            label="产品编码"
          ></el-table-column>
          <el-table-column prop="13" width="200" label="品名"></el-table-column>
          <el-table-column prop="14" label="数量"></el-table-column>
          <el-table-column prop="15" label="发货日期"></el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "",
  props: {},
  components: {},
  data() {
    return {
      loading: false,
      // 左侧导航栏菜单
      menuPlatFormList: [],
      menuCountryList: [],
      menuSiteList: [],
      showCountryList: [],
      showSiteList: [],
      lastSiteList: [],
      // 选中的菜单项
      checkedPlatform: "",
      checkedCountry: "",
      checkedSite: "",
      // 订单需求列表
      orderDemandList: [],
      // 查询相关
      queryInfo: {
        // 日期
        date: "",
        // 月份
        month: "",
        // 类型
        type: "",
        // 产品编码
        code: "",
        // 品名
        product: "",
        // 仓库类型
        stockType: "",
        // 仓库名称
        stockName: "",
        // 目的港
        purpose: "",
        // 时间段
        timeSlot: ""
      },
      // 时间段
      timeSlot: ["上旬", "中旬", "下旬"],
      // 是否禁用月份或日期
      isMonthDisabled: false,
      isDateDisabled: false,

      // 将列表数据放进下拉框里头啊
      // 类型
      typeList: [],
      codeList: [],
      productList: [],
      stockTypeList: [],
      stockNameList: [],
      purposeList: []
    };
  },
  computed: {
    orderDemandListLength() {
      return this.orderDemandList.length;
    },
    // 先用计算属性拿到日期
    seleteDate() {
      return this.queryInfo.date;
    },
    seleteMonth() {
      return this.queryInfo.month;
    }
  },
  watch: {
    // 监听日期变化 如果日期被清除，应该要让月份可以选
    seleteDate(date) {
      if (date === null) {
        this.isMonthDisabled = false;
      }
    },
    seleteMonth(month) {
      if (month === null) {
        this.isDateDisabled = false;
      }
    }
  },
  methods: {
    // 获取菜单栏数据
    getMenuList() {
      this.$api.getStockMenuList().then(res => {
        // console.log(res);
        this.menuPlatFormList = res.data.platform_list;
        this.menuCountryList = res.data.country_list;
        this.menuSiteList = res.data.site_list;

        // 页面刷新 先默认展示胤佑的 同时在导航那里添加default-active="0-0-0"
        this.showCountryList = this.menuCountryList[0];
        this.showSiteList = this.menuSiteList[0];
        this.lastSiteList = this.showSiteList[0];
        this.checkedPlatform = this.menuPlatFormList[0];
        this.checkedCountry = this.showCountryList[0];
        this.checkedSite = this.lastSiteList[0];
        this.thirdClick(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite
        );
      });
    },
    // stockMonitor() {

    // },
    onceClick(index) {
      this.showCountryList = [];
      this.showSiteList = [];
      this.showCountryList = this.menuCountryList[index];
      this.showSiteList = this.menuSiteList[index];
    },
    secondClick(index) {
      console.log(index);
      this.lastSiteList = [];
      this.lastSiteList = this.showSiteList[index];
    },
    thirdClick(platform, country, site) {
      this.checkedPlatform = platform;
      this.checkedCountry = country;
      this.checkedSite = site;
      this.loading = true;
      this.$api
        .queryOrderDemand(platform, country, site, this.queryInfo)
        .then(res => {
          this.loading = false;
          console.log(res);
          if (!res) return (this.orderDemandList = []);
          if (res.data.code !== 200) {
            if (res.data.code === 4041) {
              this.orderDemandList = [];
            }
            // this.$message.closeAll();
            // this.$message.error({
            //   message: res.data.msg
            // });
            return;
          }
          this.orderDemandList = res.data.data;
          this.typeList = this.getSelectList(11);
          this.codeList = this.getSelectList(12);
          this.stockTypeList = this.getSelectList(7);
          this.productList = this.getSelectList(13);
          this.stockNameList = this.getSelectList(9);
          this.purposeList = this.getSelectList(10);
        });
    },
    // 获取下拉框数据
    getSelectList(key) {
      const list = this.orderDemandList;
      let arr = [];
      for (let i in list) {
        arr.push(list[i][key]);
      }
      // console.log(arr);
      return arr;
    },
    search() {
      this.$api
        .queryOrderDemand(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite,
          this.queryInfo
        )
        .then(res => {
          console.log(res);
          if (!res) return;
          this.orderDemandList = res.data.data;
        });
    },
    // 日期月份二选一
    pickerChange(val) {
      if (val == "日期") {
        this.isMonthDisabled = true;
      } else {
        this.isDateDisabled = true;
      }
    }
  },
  created() {
    this.getMenuList();
  },
  mounted() {}
};
</script>
<style scoped>
#ipi-board {
  width: 100vw;
  height: 92vh;
  position: relative;
}
.left {
  position: absolute;
  width: 16vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  border-right: 1px solid black;
  top: 0;
  left: 0;
  overflow: auto;
}
.right {
  position: absolute;
  top: 0;
  left: 18vw;
  width: 80vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  padding: 24px;
  overflow: auto;
}
.left-content h3 {
  margin-top: 2vh;
  text-align: center;
}
.search {
  /* margin-bottom: 24px; */
}
.select-base {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 64%;
  font-size: 0.8rem;
}
.select-detail {
  display: flex;
  font-size: 0.8rem;
  flex-direction: wrap;
}

.select-base >>> .el-input {
  width: 70%;
}
.select-detail >>> .el-input {
  width: 70%;
}
.select {
  display: flex;
}
.select-detail .select span {
  width: 60%;
}
</style>



